header {
  width: 100%;
  position: fixed;
  padding: 15px 0;
  z-index: 9;
}
header .rounded-header {
  width: 80%;
  background-color: #171717;
  border-radius: 50px;
  padding: 0.8rem;
  margin: auto;
}
@media (max-width:1024px) {
  header .rounded-header {
    width: 90%;
  }
}
header .rounded-header .nav-links-left, header .rounded-header .nav-links-right {
  display: flex;
  gap: 5rem;
}
header .rounded-header .nav-links-left .nav-item .nav-link, header .rounded-header .nav-links-right .nav-item .nav-link {
  color: #fff;
  font-size: 2rem;
  text-decoration: none;
  padding: 1.6rem 3rem;
}
header .rounded-header .nav-links-left .nav-item .nav-link:hover, header .rounded-header .nav-links-right .nav-item .nav-link:hover {
  font-weight: bold;
}
header .rounded-header .nav-links-left .nav-item:hover, header .rounded-header .nav-links-right .nav-item:hover {
  background: #FD853A;
  border-radius: 66px;
}
header .rounded-header .logo img {
  max-height: 50px;
}

.call-icon svg {
  width: 20px;
  height: 20px;
  position: relative;
  right: 2px;
  top: 4px;
}

.enquire-btn {
  background-color: #672F90;
  color: #fff !important;
  font-size: 17px;
  padding: 14px 20px;
  font-weight: 500;
  line-height: 16px;
  border-radius: 4px;
  cursor: pointer;
}

.eq-height-row {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
}

.home-banner {
  background: #fff;
}

footer {
  float: left;
  width: 100%;
  color: #fff;
}
footer a {
  color: #fff;
}
footer .upper {
  background-color: #2b2b2b;
  padding: 10px 0;
}
footer .lower {
  background-color: #121212;
  padding: 10px 0;
  font-size: 12px;
}

.hero {
  height: 100vh;
}
.hero .container .bannerTopContentBox {
  width: fit-content;
  margin: 0 auto;
  transition: transform 0.5s ease-in-out;
}
.hero .container .bannerTopContentBox .bannerTitle {
  font-size: 95px;
  font-weight: 500;
  line-height: 100%;
  margin: 10px 0;
  position: relative;
}
.hero .container .bannerTopContentBox .bannerTitle::after {
  position: absolute;
  content: url("../images/strip2.webp");
  bottom: -60px;
  left: -60px;
}
.hero .container .devImg {
  position: relative;
}
.hero .container .devImg .imgWrapper {
  z-index: 2;
  position: relative;
}
.hero .container .devImg .imgWrapper .ideasImg {
  position: absolute;
  bottom: 0;
  width: 100%;
  transform: scale(0);
  transition: transform 0.5s ease-in-out;
  transform-origin: bottom;
  z-index: -1;
}
.hero .container .devImg::after {
  content: "";
  position: absolute;
  bottom: -520px;
  left: 50%;
  transform: translateX(-50%);
  width: 900px;
  height: 900px;
  border-radius: 100%;
  background: #FEB273;
  z-index: 1;
}
.hero .container .devImg .quoteText {
  position: absolute;
  top: 25%;
  left: 0;
  text-align: left;
  width: 30%;
  transition: top 0.5s ease-in-out;
}
.hero .container .devImg .ratingText {
  position: absolute;
  top: 25%;
  right: 0;
  text-align: right;
  width: 30%;
  transition: top 0.5s ease-in-out;
}
.hero .container .devImg .ratingText .yearCount {
  font-size: 48px;
}
.hero .container:hover .bannerTopContentBox {
  transform: translateY(200%);
}
.hero .container:hover .devImg .quoteText {
  top: -20%;
}
.hero .container:hover .devImg .ratingText {
  top: -20%;
}
.hero .container:hover .devImg .imgWrapper .ideasImg {
  transform: scale(1);
}

.overview {
  background: #fff url("../images/chocolateBg.webp") no-repeat;
  background-size: cover;
}
.overview .title {
  font-size: 48px;
  font-weight: 500;
}

@media only screen and (max-width: 1024px) {
  .hidden-md {
    display: none !important;
  }
  .visible-md {
    display: block !important;
  }
}
@media only screen and (max-width: 992px) {
  .set-transform {
    transform: unset;
    transition: unset;
  }
  header .form-container.show {
    top: 0 !important;
    bottom: 0 !important;
    display: block !important;
  }
  header .nav-links {
    display: none;
    position: fixed;
    top: 94px;
    left: 0;
    background-color: #fff;
    width: 100%;
    height: 100%;
    z-index: 999;
    transition: unset;
  }
  header .nav-links a {
    display: block !important;
    text-align: center;
    line-height: 40px !important;
    color: #fff;
    margin-top: 0px;
    margin-right: 0px;
  }
  header .lower .nav-links a {
    margin-right: 0px;
    color: #000;
  }
  header .lower .nav-links a:last-child {
    margin-right: 0px;
  }
  header .upper .enq-btn-wrapper {
    margin-right: 50px;
    margin-top: 15px;
  }
  header .upper .ctc-wrapper {
    margin-right: 5px;
  }
  header .upper .number-wrapper {
    margin-right: 15px;
    margin-top: 25px;
  }
  .menu-icon-mobile {
    display: block;
    width: 50px;
    padding: 14px 8px 4px 8px;
    background-color: #6b6b6b;
    border-radius: 0px;
    margin: 0;
    position: absolute;
    top: 15px;
    right: 0;
    height: 40px;
  }
  .menu-icon-mobile > span {
    display: block;
    height: 2px;
    width: 100%;
    margin-bottom: 4px;
    background-color: #fff;
  }
  .menu-icon-mobile.active > span:nth-child(1) {
    transform: rotate(45deg);
  }
  .menu-icon-mobile.active > span:nth-child(2) {
    transform: rotate(135deg);
    position: relative;
    top: -5px;
  }
  .menu-icon-mobile.active > span:nth-child(3) {
    display: none;
  }
  .hidden-sm {
    display: none !important;
  }
  .visible-sm {
    display: block !important;
  }
  section.padding {
    padding: 35px 0px;
  }
  #hid-on-mob {
    display: none !important;
  }
  .home-banner {
    background-image: url("https://dummyimage.com/768X1024/6b6b6b/565659&text=ipad-banner");
  }
}
@media only screen and (max-width: 640px) {
  body {
    -webkit-text-size-adjust: none;
  }
  .hidden-xs {
    display: none !important;
  }
  .visible-xs {
    display: block !important;
  }
  .menu-icon-mobile {
    height: 60px;
    padding: 25px 8px 4px 8px;
    top: -5px;
  }
  header {
    padding: 5px 0;
  }
  header .nav-links {
    padding: 10px 0;
    top: 55px;
  }
  header .brand-logo img {
    width: 90px;
  }
  header .form-container {
    position: fixed !important;
    left: 0 !important;
    right: 0 !important;
    top: 100% !important;
    bottom: -100% !important;
    width: 100% !important;
    padding: 30px 0 !important;
    -ms-transform: translate(0, 0);
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
    z-index: 300;
    background-color: rgba(255, 254, 254, 0.97) !important;
  }
  header .form-container .frmclose {
    display: inline-block;
    position: absolute;
    top: 10px;
    right: 10px;
    color: #000;
    font-size: 20px;
  }
  header .form-container.show {
    top: 0 !important;
    bottom: 0 !important;
    display: block !important;
  }
  footer {
    padding-bottom: 30px;
  }
  footer .lower {
    font-size: 10px;
  }
  footer .lower div {
    text-align: center !important;
  }
  .mob-footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    background-color: #fff;
    font-size: 14px;
    font-weight: 600;
    z-index: 999999;
  }
  .mob-footer a {
    display: block;
    line-height: 30px;
  }
  .mob-footer a.mob-enq-btn {
    background-color: #6b6b6b;
    color: #fff;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
  }
  .mob-footer a.mob-call-btn {
    background-color: #fff;
    color: #6b6b6b;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
  }
  .home-banner {
    background-image: url("https://dummyimage.com/320X480/6b6b6b/565659&text=mobile-banner");
  }
}